.grid-container {
  background-color: #e5e5e5;
  border-radius: 40px;
  display: grid;
  gap: 16px;
  grid-template-columns: auto auto auto;
  justify-content: center;
  padding: 16px 5px;
}

.image-style {
  border: 3px solid transparent;
  border-radius: 40px;
  height: auto;
  width: 70px;
}
